<template><section><html><head></head><body><h1>量算工具</h1>
<ul>
<li><code v-pre="">vc-measure-distance</code> 组件用于距离量算。</li>
<li><code v-pre="">vc-measure-area</code> 组件用于量算面积</li>
<li><code v-pre="">vc-measure-height</code> 组件用于量算高程。</li>
</ul>
<h2>示例</h2>
<h3>测量距离、面积、高度</h3>
<h4>预览</h4>
