  <template>
    <div class="viewer">
      <vc-viewer @ready="ready" scene3-d-only="">
        <vc-measure-distance :clamp-to-ground="clampToGround" ref="measureDistance" @activeevt="activeEvt" @measureevt="measureEvt" @movingevt="movingEvt" :remove-last-position="removeLastPosition" :editable="editable"></vc-measure-distance>
        <vc-measure-area ref="measureArea" @activeevt="activeEvt" @measureevt="measureEvt" @movingevt="movingEvt" :clamp-to-ground="clampToGround" :remove-last-position="removeLastPosition" :editable="editable"></vc-measure-area>
        <vc-measure-height ref="measureHeight" @activeevt="activeEvt" @measureevt="measureEvt" @movingevt="movingEvt" :remove-last-position="removeLastPosition" :editable="editable"></vc-measure-height>
        <vc-primitive-tileset :url="modelUrl" @ready-promise="readyPromise"></vc-primitive-tileset>
      </vc-viewer>
      <div class="demo-tool">
        <md-button class="md-raised md-accent" @click="toggle('measureDistance')">{{ distanceMeasuring ? '停止' : '距离' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureArea')">{{ areaMeasuring ? '停止' : '面积' }}</md-button>
        <md-button class="md-raised md-accent" @click="toggle('measureHeight')">{{ heightMeasuring ? '停止' : '高度' }}</md-button>
        <md-button class="md-raised md-accent" @click="clear">清除</md-button>
        <md-checkbox v-model="clampToGround" class="md-primary">贴地</md-checkbox>
        <md-checkbox v-model="removeLastPosition" class="md-primary">移除最后一个点</md-checkbox>
      </div>
    </div>
  </template>
  
</doc-preview>
